@import "@/scss/modules/_variables.scss";
.starRating {
  display: flex;
  gap: 4px;
  width: auto;
  margin-bottom: 35px;
}

.filled {
  font-size: 36px;
  color: gold;
  border-radius: 50%;
  padding: 2px;
}

.empty {
  font-size: 36px;
  color: gray;
  border-radius: 50%;
  background-color: transparent;
  padding: 2px;
}
